<script setup>
import goodMusic from '../assets/music/verno-4.mp3'
import badMusic from '../assets/music/otvet-ne-vernyi.mp3'

import { ref, onMounted, onBeforeUnmount } from 'vue'
import Button from './Button.vue'

const index = ref(0)
const props = defineProps(['videoArr'])
const emit = defineEmits(['userAnswer', 'showResult'])

const isShowChooseBtn = ref(false)
const isCorrect = ref(false)
const isShowBat = ref(false)
const isShowNextBtn = ref(false)
const goodAudio = ref(null)
const badAudio = ref(null)

// Ссылки на видео
const videoRefs = ref([])

const checkUserAnswer = answer => {
	const correctAnswr = props.videoArr[index.value].isGood
	if (correctAnswr === answer) {
		isCorrect.value = true
		goodAudio.value.currentTime = 0.7
		goodAudio.value.play()
		emit('userAnswer', { index: index.value, result: false })
	} else {
		isCorrect.value = false
		badAudio.value.play()
		emit('userAnswer', { index: index.value, result: true })
	}
	isShowBat.value = true
	isShowChooseBtn.value = false
	isShowNextBtn.value = true
}

const nextVideo = () => {
	if (index.value < props.videoArr.length - 1) {
		index.value += 1
	} else {
		showResult()
	}
	isShowNextBtn.value = false
}

const hideAll = () => {
	isShowChooseBtn.value = false
	isShowBat.value = false
}

const showResult = () => {
	emit('showResult')
}

// Предзагрузка видео
const preloadVideosSequentially = async () => {
	for (const video of props.videoArr) {
		await new Promise((resolve, reject) => {
			const videoElement = document.createElement('video')
			videoElement.src = video.video
			videoElement.preload = 'auto'
			videoElement.style.display = 'none'
			document.body.appendChild(videoElement)

			// Ждем, пока загрузится первый кадр видео
			videoElement.addEventListener('loadeddata', () => {
				resolve()
			})

			// Обработка ошибок
			videoElement.addEventListener('error', () => {
				reject(new Error(`Ошибка загрузки видео: ${video.video}`))
			})
		})
	}
}

// Очистка видео при размонтировании компонента
const cleanupVideos = () => {
	videoRefs.value.forEach(video => {
		if (video) {
			video.remove()
		}
	})
}

onMounted(() => {
	preloadVideosSequentially()
})

onBeforeUnmount(() => {
	cleanupVideos()
})
</script>

<template>
	<audio ref="goodAudio" :src="goodMusic"></audio>
	<audio ref="badAudio" :src="badMusic"></audio>
	<div class="container">
		<!-- Анимация для изображения -->
		<Transition name="fade">
			<div v-if="isShowBat" class="container__img">
				<img v-if="isCorrect" src="../assets/img/good.png" alt="" />
				<img v-else src="../assets/img/bad.png" alt="" />
			</div>
		</Transition>

		<div class="container__video">
			<video
				ref="videoRefs[index]"
				@ended="isShowChooseBtn = true"
				@play="hideAll"
				autoplay="true"
				:src="videoArr[index].video"
			></video>
		</div>

		<!-- Анимация для кнопок выбора и кнопки "Дальше" -->
		<Transition name="fade" mode="out-in">
			<div class="container__btn" v-if="isShowChooseBtn">
				<Button font-size="1.2em" @click="checkUserAnswer(true)"
					>Хороший <br />
					преподаватель</Button
				>
				<Button font-size="1.2em" @click="checkUserAnswer(false)"
					>Некомпетентный <br />
					преподаватель</Button
				>
			</div>
			<div class="container__btn" v-else-if="isShowNextBtn">
				<Button v-if="index === videoArr.length - 1" @click="showResult"
					>Посмотреть результат</Button
				>
				<Button v-else @click="nextVideo">Дальше</Button>
			</div>
		</Transition>
	</div>
</template>

<style scoped>
.container {
	position: relative;
}

.container__video {
	width: 100%;
	position: absolute;
	z-index: -1;
}

.container__video video {
	width: 100%;
}

.container__btn {
	display: flex;
	justify-content: space-around;
	position: relative;
	top: 44vw;
}

.container__img {
	position: absolute;
	right: 0vw;
	top: 0vw;
}

.container__img img {
	width: 20vw;
}

/* Анимация появления и исчезновения */
.fade-enter-active,
.fade-leave-active {
	transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
	opacity: 0;
}
</style>
