<script setup>
import StartPage from './components/StartPage.vue'
import Game from './components/Game.vue'
import Result from './components/Result.vue'
import { ref, computed } from 'vue'

import video1 from './assets/video/1.mp4'
import video2 from './assets/video/2.mp4'
import video3 from './assets/video/3.mp4'
import video4 from './assets/video/4.mp4'
import video5 from './assets/video/5.mp4'
import video6 from './assets/video/6.mp4'
import video7 from './assets/video/7.mp4'
import video8 from './assets/video/8.mp4'
import video9 from './assets/video/9.mp4'
import video10 from './assets/video/10.mp4'

const videoArr = ref([
	{
		video: video1,
		isGood: false,
		userIsFail: null,
	},
	{
		video: video2,
		isGood: false,
		userIsFail: null,
	},
	{
		video: video3,
		isGood: true,
		userIsFail: null,
	},
	{
		video: video4,
		isGood: true,
		userIsFail: null,
	},
	{
		video: video5,
		isGood: false,
		userIsFail: null,
	},
	{
		video: video6,
		isGood: false,
		userIsFail: null,
	},
	{
		video: video7,
		isGood: false,
		userIsFail: null,
	},
	{
		video: video8,
		isGood: true,
		userIsFail: null,
	},
	{
		video: video9,
		isGood: true,
		userIsFail: null,
	},
	{
		video: video10,
		isGood: false,
		userIsFail: null,
	},
])

const isStart = ref(false)
const isShowResult = ref(false)
const addUserAnswer = e => {
	videoArr.value[e.index].userIsFail = e.result
}

const userFailCount = computed(() => {
	return videoArr.value.filter(item => item.userIsFail === true).length
})

const filterVideo = () => {
	videoArr.value = videoArr.value.filter(item => item.userIsFail === true)
	isShowResult.value = false
}
</script>

<template>
	<Transition mode="out-in">
		<div v-if="!isStart"><StartPage @start="isStart = true"></StartPage></div>
		<div v-else>
			<Transition mode="out-in">
				<div v-if="!isShowResult">
					<Game
						:videoArr="videoArr"
						@userAnswer="addUserAnswer"
						@showResult="isShowResult = true"
					></Game>
				</div>
				<div v-else>
					<Result :count="userFailCount" @filter="filterVideo"></Result>
				</div>
			</Transition></div
	></Transition>
</template>

<style scoped>
.v-enter-active,
.v-leave-active {
	transition: opacity 0.5s ease;
}

.v-enter-from,
.v-leave-to {
	opacity: 0;
}
</style>
